*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    height: 100vh;
    /* font-family: 'Poppins'; */
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background: rgb(24, 24, 24);
}
body{
    overflow-x: hidden;
}
.webgl
{
    position: absolute;
    top: 0;
    left: 0;
    outline: none;
    mix-blend-mode: exclusion;
}
.container{
    height: 100vh;
    display: grid;
    place-items: center;
}
h1{
    font-size: 7rem;
    text-transform: uppercase;
    color: white;
}
section{height: 100vh;}
